import React, { Component } from 'react';
import uuid from 'uuid';
import PropTypes from 'prop-types';


export default class Formulario extends Component {

  descripcionRef;
  montoRef;
  fechaRef;
  constructor(props) {

    super(props);
    this.descripcionRef = React.createRef();
    this.montoRef = React.createRef();
    this.fechaRef = React.createRef();
    this.state = {
      error: false
    }
  }
  enviarGasto = (e) => {
    e.preventDefault();

    if(this.descripcionRef.current.value=='' ||
    this.montoRef.current.value=='' ||
    this.fechaRef.current.value=='' ){
      this.setState({
        error:true,
      })
      return;
    }
    let gasto = {
      id: uuid(),
      descripcion: this.descripcionRef.current.value,
      monto: this.montoRef.current.value,
      fecha: this.fechaRef.current.value
    }
    this.props.enApp(gasto);
    e.currentTarget.reset();
    this.setState({
      error:false,
    })

  }


  render() {
    return (

      <form onSubmit={this.enviarGasto}>
        <fieldset>
          <legend>Gastos y Presupuestos</legend>
          <div className="form-group row">
            <label htmlFor="staticEmail" className="col-sm-2 col-form-label">Email</label>
            <div className="col-sm-10">
              <input type="text" readOnly className="form-control-plaintext" id="staticEmail" value="email@example.com" />
            </div>
          </div>
          <div className="form-group">
            <label htmlFor="descripcion">Descripci√≥n del Gasto</label>
            <input ref={this.descripcionRef} type="text"  className="form-control" id="descripcion" aria-describedby="emailHelp" placeholder="Ejemplo: comida, ropa ..." />
          </div>
          <div className="form-group">
            <label htmlFor="gasto">Monto del Gasto</label>
            <input ref={this.montoRef} type="number" required className="form-control" id="gasto" placeholder="Ejemplo: S/.100" />
          </div>
          <div className="form-group">
            <label htmlFor="fecha">Fecha de Compra</label>
            <input ref={this.fechaRef} type="date" required className="form-control" id="fecha" />
          </div>

          {
            this.state.error ?
            <div className="alert alert-danger mt-3">
            todos campos son obligatorios
            </div> : null
          }
          <button type="submit" className="btn btn-primary mb-3">Crear Gasto</button>

        </fieldset>
      </form>

    )
  }
}


Formulario.propTypes = {
  enApp : PropTypes.func.isRequired, 
}