import React, { Component } from 'react'
import Header from './components/Header';
import Formulario from './components/Formulario';
import Lista from './components/Lista';

export default class App extends Component {

constructor(props){
  super(props);
  this.state={
    gastos:[],    
    presupuesto:1000,
    resto:1000,

  }
}

  enApp = (objGasto)=>{
    let{gastos} = this.state;
    gastos.push(objGasto);
    let newResto=this.state.resto - objGasto.monto;
    this.setState({
      gastos,
      resto:newResto 
    });

    
  }
  render() {
    return (
      <React.Fragment>
        
        <Header />


        <div className="container mt-5">
          <div className="row">
            <div className="col-md-6 border-primary card ">
              <Formulario enApp={this.enApp} />
            </div>
            <div className="col-md-6 border-primary card">
              <Lista gastos={this.state.gastos}
                    presupuesto={this.state.presupuesto}
                    resto={this.state.resto}/>
            </div>
          </div>

        </div>
      </React.Fragment>
    )
  }
}
